export default {
    methods: {
        resetCameraPosition() {
            const screenWidth = window.screen.width;
            let z;
            if (screenWidth < 2000) {
                z = 290;
            } else {
                z = 200;
            }
            this.camera.position.z = z;
        },
        addBackground() {
            const geom = this.initGeometry('Cube', 700, 400, 2);
            const mat = this.initMaterial('MeshBasic', { color: 0xffffff });
            const texture = this.loadTexture('img/bg.png');
            const picMat = new THREE.MeshBasicMaterial({ map: texture });
            const face = [];
            for (let i = 0; i < 5; i++) {
                if (i === 4) {
                    face.push(picMat);
                } else {
                    face.push(mat);
                }
            }
            const plane = new THREE.Mesh(geom, face);
            plane.position.z = -25;
            this.scene.add(plane);
        },
        addAxes(len) { // 
            const axes = this.initAxes(len);
            this.scene.add(axes);
        },
        addControl() { // 
            const control = this.initControls('Trackball', this.camera, this.renderer.domElement, {
                zoomSpeed: 1.0,
                panSpeed: 0.5,
                // maxDistance: 1500,
                minDistance: 1,
            });
            return control;
        },
        addPoints(geom, rgb, size = 4) {
            const { r, g, b } = rgb;
            const str = `rgba(${r}, ${g}, ${b}`;
            const mapss = [{
                pro: 0,
                color: `${str},1)`,
            }, {
                pro: 0.9,
                color: `${str},0.6)`,
            }, {
                pro: 1,
                color: `${str},0)`,
            }];
            const points = this.createPoints(geom, {
                size,
            }, mapss);
            return points;
        },
    },
    mounted() {
        this.initWebGL();
    },
    beforeDestroy() {
        this.renderer.dispose();
        this.renderer.forceContextLoss();
        this.renderer.context = null;
        this.renderer.domElement = null;
        this.renderer = null;
    },
};